.main-wrap {
  display: flex;

  main {
    display: flex;
  }
}

.main {
  position: relative;
  display: flex;
  flex-grow: 1;
}

.main-lime {
  position: absolute;
  top: -100px;
  left: 50%;
  transform: translate(-50%);
  width: Max(Min(903px, 47%), 766px);
  height: 100%;
  object-fit: contain;
  //object-position: center top;

  @media (max-width: $viewport-s) {
    width: 100%;
  }
}

.main-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;

/*  @media (max-width: $viewport-s) {
    top: initial;
    margin-top: 60%;
  }*/
}
